import { getParamsToSearch } from "perkz-tool";
export class Service {
    headers;
    timeout;
    // 
    options = {
        credentials: "include"
    };
    // 
    timer = null;
    controller = new AbortController();
    constructor(headers = {}, timeout = 10000) {
        this.headers = headers;
        this.timeout = timeout;
        this.options.headers = headers;
    }
    timeOut = () => new Promise((resolve, reject) => {
        this.timer = setTimeout(() => {
            this.abort();
            reject();
        }, this.timeout);
    });
    fetch = (url, options) => {
        return new Promise((resolve, reject) => {
            fetch(url, {
                ...options,
                signal: this.controller.signal
            }).then((res) => {
                this.timer && clearTimeout(this.timer);
                try {
                    res.json().then((res) => resolve(res));
                }
                catch (e) {
                    return reject(e);
                }
            }).catch((e) => {
                this.timer && clearTimeout(this.timer);
                return reject(e);
            });
        });
    };
    //  url
    modeA = (method, url, data) => {
        const search = getParamsToSearch(data);
        const _url = search ? `${url}?${search}` : url;
        return Promise.race([
            this.fetch(_url, { ...this.options, method }),
            this.timeOut()
        ]);
    };
    //  body
    modeB = (method, url, data) => {
        return Promise.race([this.fetch(url, { ...this.options, method, body: JSON.stringify(data) }), this.timeOut()]);
    };
    // get 
    get = (url, data) => this.modeA("GET", url, data);
    // post 
    post = (url, data) => this.modeB("POST", url, data);
    // put 
    put = (url, data) => this.modeB("PUT", url, data);
    // delete 
    delete = (url, data) => this.modeA("DELETE", url, data);
    // 
    abort = () => {
        this.controller.abort();
        this.controller = new AbortController();
        return this;
    };
}
