export const getParamsToSearch = (params) => {
    let search = "";
    for (const p in params) {
        if (params.hasOwnProperty(p) && (params[p] || params[p] === 0 || params[p] === "")) {
            search += `${p}=${params[p]}&`;
        }
    }
    search = search.substr(0, search.length - 1);
    return search;
};
export const getSearchToParams = (search) => {
    const params = {};
    if (search) {
        const a = (search.replace("?", "")).split("&");
        a.forEach((item) => {
            const keyValue = item.split("=");
            params[keyValue[0]] = decodeURI(keyValue[1]);
        });
    }
    return params;
};
export const arrayObjReduce = (arr) => {
    const obj = {};
    arr = arr.reduce(function (item, next) {
        if (!obj[next.key]) {
            obj[next.key] = item.push(next);
        }
        return item;
    }, []);
    return arr;
};
export function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => reject(error);
    });
}
export function getImageBase64(src) {
    return new Promise((resolve, reject) => {
        const image = new Image();
        image.crossOrigin = "*";
        image.src = src;
        image.onload = () => {
            const canvas = document.createElement("canvas");
            canvas.width = image.width;
            canvas.height = image.height;
            const ctx = canvas.getContext("2d");
            ctx.drawImage(image, 0, 0, image.width, image.height);
            const dataURL = canvas.toDataURL("image/png");
            canvas.remove();
            image.remove();
            return resolve(dataURL);
        };
        image.onerror = (error) => reject(error);
    });
}
export function getBinary(file) {
    return new Promise((resolve, reject) => {
        getBase64(file)
            .then((res) => {
            const arr = res.split(",");
            const mime = arr[0].match(/:(.*?);/)[1];
            const bstr = atob(arr[1]);
            let n = bstr.length;
            const u8arr = new Uint8Array(n);
            while (n--) {
                u8arr[n] = bstr.charCodeAt(n);
            }
            return resolve(new Blob([u8arr], { type: mime }));
        })
            .catch(reject);
    });
}
export const copyText = (value) => {
    return new Promise((resolve) => {
        const oInput = document.createElement("input");
        oInput.value = value;
        document.body.appendChild(oInput);
        oInput.select();
        document.execCommand("Copy");
        oInput.className = "oInput";
        oInput.style.display = "none";
        resolve("success");
    });
};
export function getCookie(cookieName) {
    const name = cookieName + "=";
    const cookies = document.cookie.split(";");
    for (let i = 0; i < cookies.length; i++) {
        const c = cookies[i].trim();
        if (c.indexOf(name) === 0) {
            return c.substring(name.length, c.length);
        }
    }
    return "";
}
export function setCookie(cookieName, cookieValue, domain = "", path = "/") {
    document.cookie = `${cookieName}=${cookieValue}; expires=Thu, 18 Dec 2043 12:00:00 GMT; domain=${domain}; path=${path}`;
}
