export const getObjValue = (obj, keys) => {
    let value = obj;
    const keyArray = (keys.split(".")).filter(Boolean);
    if (keyArray.length) {
        for (let i = 0; i < keyArray.length; i++) {
            if (value && value[keyArray[i]]) {
                value = value[keyArray[i]];
            }
            else {
                value = undefined;
                return undefined;
            }
        }
    }
    else {
        return value;
    }
    return value;
};
export const randAChar = () => {
    const n = Math.random() * 10;
    const s = String.fromCharCode(Math.floor(Math.random() * 26) + "a".charCodeAt(0));
    if (n > 5) {
        return s;
    }
    else {
        return (s).toUpperCase();
    }
};
// 
export const randCode = (n = Math.random() * 5) => {
    let s = "";
    for (let i = 0; i < n; i++) {
        s += randAChar();
    }
    return ((s +
        Math.ceil(Math.random() * 100) +
        new Date().getTime() +
        Math.ceil(Math.random() * 100))
        .replace(/0/g, "h"))
        .replace("9", "f") +
        randAChar();
};
