export const removeArrayItem = (array, target) => {
    const _array = [...array];
    const index = _array.indexOf(target);
    if (index > -1) {
        _array.splice(index, 1);
        return _array;
    }
    else {
        return _array;
    }
};
// 
export const deBounce = (fn, delay) => {
    let timer = null;
    return function (...args) {
        if (timer) {
            clearTimeout(timer);
        }
        timer = setTimeout(() => {
            fn(...args);
        }, delay);
    };
};
// 
export const throttle = (fn, delay) => {
    let flag = true;
    return function (...args) {
        if (!flag)
            return;
        flag = false;
        setTimeout(() => {
            fn(...args);
            flag = true;
        }, delay);
    };
};
// 
export function duplicateRemoval(data = []) {
    const set = new Set();
    data.forEach((item) => set.add(item));
    return [...set];
}
