import { applyMiddleware, combineReducers, createStore } from "redux";
import thunk from "redux-thunk";
const createState = (map) => {
    const newMap = { ...map };
    newMap.reducers = newMap.reducers ? newMap.reducers : {};
    const newReducers = {};
    // stateset
    newReducers[`${newMap["namespace"]}/setState`] = (_state, { state }) => ({ ..._state, ...state });
    // stateset
    Object.keys(newMap.state).forEach((item) => {
        newReducers[`${newMap["namespace"]}/set/${item}`] = (_state, state) => ({
            ..._state,
            [item]: state[item]
        });
    });
    // reducers
    Object.entries(newMap.reducers).forEach((item) => {
        newReducers[`${newMap["namespace"]}/${item[0]}`] = item[1];
    });
    newMap.reducers = newReducers;
    return (state = newMap.state, action) => {
        if (newMap.reducers && newMap.reducers[action.type]) {
            return newMap.reducers[action.type](state, action);
        }
        else {
            return state;
        }
    };
};
// namespace
const createReducers = (reducers) => {
    const newReducers = {};
    Object.keys(reducers).forEach((key) => {
        newReducers[reducers[key]["namespace"]] = createState(reducers[key]);
    });
    return newReducers;
};
// 
export const createStores = (stores) => createStore(combineReducers(createReducers({ ...stores })), applyMiddleware(thunk));
